ul, li {
  list-style: none;
  padding: 0;
}

.container {
  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 1rem;
  background: #eaf6ff;
  /* From https://css.glass */
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  padding: 3rem 0;
  width: 100vw;
}

.wrapper {
  padding: 2rem;
  border-radius: 15px;
}

h1 {
  color: #333;
  font-size: 1.5rem;
  font-family: sans-serif;
  font-family: "Montserrat Arabic";
}

.sessions {
  margin-top: 2rem;
  border-radius: 12px;
  position: relative;
}

li {
  padding-bottom: 1.5rem;
  border-right: 1px solid #abaaed;
  position: relative;
  padding-right: 20px;
  margin-right: 10px;
}
li:last-child {
  border: 0px;
  padding-bottom: 0;
}
li:before {
  content: "";
  width: 15px;
  height: 15px;
  background: white;
  border: 1px solid #4e5ed3;
  box-shadow: 3px 3px 0px #bab5f8;
  box-shadow: 3px 3px 0px #bab5f8;
  border-radius: 50%;
  position: absolute;
  right: -10px;
  top: 0px;
}

@media screen and (min-width: 1200px) {
  .sessions {
    display: grid;
    grid-auto-flow: column;
    gap: 0px;
  }
  .sessions li {
    border: none;
    padding-top: 1.5rem;
    border-top: 1px solid #abaaed;
  }
  .sessions li::before {
    top: -8px;
  }
}
.time {
  color: #2a2839;
  font-weight: 500;
  font-family: "Montserrat";
}
@media screen and (min-width: 601px) {
  .time {
    font-size: 0.9rem;
  }
}
@media screen and (max-width: 600px) {
  .time {
    margin-bottom: 0.3rem;
    font-size: 0.85rem;
  }
}

p {
  color: #4f4f4f;
  font-family: sans-serif;
  line-height: 1.5;
  margin-top: 0.4rem;
  font-family: "Montserrat Arabic";
}
@media screen and (max-width: 600px) {
  p {
    font-size: 0.9rem;
  }
}